import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Button, Link,  Div,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Recent from '@vkontakte/icons/dist/24/recent';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';


const osname = platform();

const Lol = props => (
  <Panel id={props.id}>
    <PanelHeader
      left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
    >
    Макароны по-флотски
    </PanelHeader>
<Group>
              <List>
                <Cell before={<Icon24Favorite fill="#ffa500"/>} indicator="4.7">Рейтинг </Cell>
             </List>
            </Group>


<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link>Не указан</Link>}>Автор</Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://vk.com/great.food">vk.com/great.food</Link>}>Ресурс </Cell>
<Cell before={<Icon24Recent fill="#4bb34b" />} indicator="~ 45 минут">Время приготовления </Cell>

</List>
</Group>




<Group>
<Div> Ингредиенты: 
<br />• Мясной фарш 500 г.
<br />• Макароны 400 г.
<br />• Репчатый лук 1 шт.
<br />• Растительное масло по вкусу.
<br />• Соль, перец по вкусу.
</Div>
</Group>

         <Group>
<Div> Рецепт: 
<br />• Отварить макароны до готовности в подсоленной воде. 
<br />• Мелко нарезать лук. Разогреть сковороду с растительным маслом, выложить мясной фарш. 
<br />• Если говядина жесткая, можно добавить немного кипящей воды. 
<br />• Когда мясо начнет подрумяниваться, добавить нарезанный лук. 
<br />• Посолить и поперчить. Тушить до мягкости лука.
<br />• Добавить макароны, хорошо перемешать. 
<br />• За минуту до готовности добавить нарезанную петрушку. 
<br />•  Макароны по-флотски готовы!

</Div>
</Group>
  </Panel>
);
Lol.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
};

export default Lol;
