import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Spinner, Footer} from '@vkontakte/vkui';

const Load = props => (
  <Panel id={props.id}>
    <PanelHeader>
      Подключение
    </PanelHeader>
    <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
      <Spinner size="regular" style={{ marginTop: 20 }} />
    </div>

<Footer> 
Если загрузка идет очень долго, значит Вы зашли в Сервис не через ВКонтакте. 
</Footer>


  </Panel>
);

Load.propTypes = {
  id: PropTypes.string.isRequired,
};

export default Load;